/** A small library to class up log messages.
 * @author https://github.com/doubleactii
 * @license Logger does not have a license at this time. For licensing contact the author
 */

/**
 * The Logger class.
 * A small library to class up log messages.
 * @example
 * ```typescript
 * import { Logger } from './logger.min.js';
 * const logger = new Logger();
 * logger.registerType('EXAMPLE', logger.FG_BLUE);
 * logger.prefix('EXAMPLE').log('Logger started');
 * // Assert example
 * const a = 10, b = 5;
 * logger.assert(a % b == 1, 'error at a%b==1');
 * logger.assert(a > b, 'error at a>b');
 * logger.assert(b > a, 'error at b>a');
 * // Counter example
 * for (let i = 0; i < 4; i ++) {
 * 	logger.count('Example Label');
 * }
 * logger.countReset('Example Label');
 * logger.count('Example Label');
 * // Debug example
 * logger.debug('Debug example', 1, true, [1, 2], { foo: 1, bar: 2 });
 * logger.prefix('EXAMPLE').debug('Debug example', 1, true, [1, 2], { foo: 1, bar: 2 });
 * // Error example
 * logger.error('Error example', 1, true, [1, 2], { foo: 1, bar: 2 });
 * logger.prefix('EXAMPLE').error('Error example', 1, true, [1, 2], { foo: 1, bar: 2 });
 * // Warn example
 * logger.warn('Warn example', 1, true, [1, 2], { foo: 1, bar: 2 });
 * logger.prefix('EXAMPLE').warn('Warn example', 1, true, [1, 2], { foo: 1, bar: 2 });
 * // Info example
 * logger.info('Info example', 1, true, [1, 2], { foo: 1, bar: 2 });
 * logger.prefix('EXAMPLE').info('Info example', 1, true, [1, 2], { foo: 1, bar: 2 });
 * // Log example
 * logger.log('Log example', 1, true, [1, 2], { foo: 1, bar: 2 });
 * logger.prefix('EXAMPLE').log('Log example', 1, true, [1, 2], { foo: 1, bar: 2 });
 * // Group example
 // group and groupCollapsed are interchangeable
 * logger.prefix('EXAMPLE').log("This is the outer level");
 * logger.prefix('EXAMPLE').groupCollapsed('Example Label2');
 * logger.prefix('EXAMPLE').prefix('EXAMPLE').log("Level 2");
 * logger.prefix('EXAMPLE').groupCollapsed('Example Label3');
 * logger.prefix('EXAMPLE').log("Level 3");
 * logger.prefix('EXAMPLE').warn("More of level 3");
 * logger.groupEnd(); // No label needed for groupEnd in standard console
 * logger.prefix('EXAMPLE').log("Back to level 2");
 * logger.groupEnd(); // No label needed for groupEnd in standard console
 * logger.prefix('EXAMPLE').log("Back to the outer level");
 * // Table example
 * logger.table(["apples", "oranges", "bananas"]);
 * // Time example
 * logger.time("Time example");
   // Time passes by
 * logger.timeLog("Time example");
 * * logger.prefix('EXAMPLE').time("Time example");
   // Time passes by
 * logger.prefix('EXAMPLE').timeLog("Time example");
 * // Trace example
 * function foo() {
 * 	function bar() {
 * 		logger.trace();
 * 	}
 * 	bar();
 * }
 * foo();
 * // Clear the console example
 * setTimeout(() => { logger.clear(); }, 5000);
 * ```
 */
export class Logger {
    // Settings
    /**
     * @internal
     * RESET code in ANSI code
     */
    private RESET: string;
    /**
     * BRIGHT code in ANSI code
     */
    public BRIGHT: string;
    /**
     * DIM code in ANSI code
     */
    public DIM: string;
    /**
     * UNDERSCORE code in ANSI code
     */
    public UNDERSCORE: string;
    /**
     * BLINK code in ANSI code
     */
    public BLINK: string;
    /**
     * REVERSE code in ANSI code
     */
    public REVERSE: string;
    /**
     * HIDDEN code in ANSI code
     */
    public HIDDEN: string;

    // Foreground
    /**
     * FG_BLACK code in ANSI code
     */
    public FG_BLACK: string;
    /**
     * FG_RED code in ANSI code
     */
    public FG_RED: string;
    /**
     * FG_GREEN code in ANSI code
     */
    public FG_GREEN: string;
    /**
     * FG_YELLOW code in ANSI code
     */
    public FG_YELLOW: string;
    /**
     * FG_BLUE code in ANSI code
     */
    public FG_BLUE: string;
    /**
     * FG_MAGENTA code in ANSI code
     */
    public FG_MAGENTA: string;
    /**
     * FG_CYAN code in ANSI code
     */
    public FG_CYAN: string;
    /**
     * FG_WHITE code in ANSI code
     */
    public FG_WHITE: string;
    /**
     * FG_GRAY code in ANSI code
     */
    public FG_GRAY: string;

    // Background
    /**
     * BG_BLACK code in ANSI code
     */
    public BG_BLACK: string;
    /**
     * BG_RED code in ANSI code
     */
    public BG_RED: string;
    /**
     * BG_GREEN code in ANSI code
     */
    public BG_GREEN: string;
    /**
     * BG_YELLOW code in ANSI code
     */
    public BG_YELLOW: string;
    /**
     * BG_BLUE code in ANSI code
     */
    public BG_BLUE: string;
    /**
     * BG_MAGENTA code in ANSI code
     */
    public BG_MAGENTA: string;
    /**
     * BG_CYAN code in ANSI code
     */
    public BG_CYAN: string;
    /**
     * BG_WHITE code in ANSI code
     */
    public BG_WHITE: string;
    /**
     * BG_GRAY code in ANSI code
     */
    public BG_GRAY: string;
    /**
     * @internal
     * The type's spacing
     */
    private TYPE_SPACER_LENGTH: number;
    /**
     * The types of this logger
     * @internal
     */
    private types: { [key: string]: string };
    /**
     * @internal
     * The type of message to display
     */
    private currentType: string;
    /**
     * @internal
     * The space char
     */
    private SPACE_CHAR: string;
    // https://talyian.github.io/ansicolors/
    // \x1b[38;5;[n]m is foreground, \x1b[48;5;[n]m is background
    // The first 16 entries are the basic color table again
    // The next 216 entries are a 6x6x6 cube
    // The final 24 entries are a grayscale ramp
    /**
     * An object containing all ANSI foreground color codes (0-255)
     */
    public FG_COLORS: { [key: number]: string };
    /**
     * An object containing all ANSI background color codes (0-255)
     */
    public BG_COLORS: { [key: number]: string };

    /**
     * Creates an instance of Logger.
     * @param pTypes - Array of type objects to register
     */
    constructor(pTypes?: { type: string, ansi: string }[]) {
        // Settings
        this.RESET = "\x1b[0m";
        this.BRIGHT = "\x1b[1m";
        this.DIM = "\x1b[2m";
        this.UNDERSCORE = "\x1b[4m";
        this.BLINK = "\x1b[5m";
        this.REVERSE = "\x1b[7m";
        this.HIDDEN = "\x1b[8m";

        // Foreground
        this.FG_BLACK = "\x1b[30m";
        this.FG_RED = "\x1b[31m";
        this.FG_GREEN = "\x1b[32m";
        this.FG_YELLOW = "\x1b[33m";
        this.FG_BLUE = "\x1b[34m";
        this.FG_MAGENTA = "\x1b[35m";
        this.FG_CYAN = "\x1b[36m";
        this.FG_WHITE = "\x1b[37m";
        this.FG_GRAY = "\x1b[90m";

        // Background
        this.BG_BLACK = "\x1b[40m";
        this.BG_RED = "\x1b[41m";
        this.BG_GREEN = "\x1b[42m";
        this.BG_YELLOW = "\x1b[43m";
        this.BG_BLUE = "\x1b[44m";
        this.BG_MAGENTA = "\x1b[45m";
        this.BG_CYAN = "\x1b[46m";
        this.BG_WHITE = "\x1b[47m";
        this.BG_GRAY = "\x1b[100m";
        
        this.TYPE_SPACER_LENGTH = 13;
        
        this.types = {
            default: this.FG_WHITE
        };
        
        this.currentType = '';
        
        this.SPACE_CHAR = ' ';
        
        this.FG_COLORS = {};
        for (let i = 0; i <= 255; i++) {
            this.FG_COLORS[i] = '\x1b[38;5;' + i + 'm';
        }
        
        this.BG_COLORS = {};
        for (let i = 0; i <= 255; i++) {
            this.BG_COLORS[i] = '\x1b[48;5;' + i + 'm';
        }
        if (Array.isArray(pTypes)) {
            this.registerTypes(pTypes);
        }
    }
    
    /**
     * Apply a prefix to the message
     * @param pType - The type of log message
     * @returns The logger instance for chaining.
     */
    public prefix(pType: string): this {
        if (typeof(pType) === 'string') this.currentType = pType;
        return this;
    }

    /**
     * Log the message via pMethod
     * @internal
     * @param pMethod - The console method to use
     * @param pMessage - string, or array of strings of messages
     */
    private message(pMethod: string = 'log', ...pMessage: any[]): void {
        const TYPE: string = (this.currentType ? this.currentType : '');
        // If the type is empty then we do not need to add the spacer
        const IS_EMPTY_TYPE: boolean = (TYPE.length === 0);
        if (IS_EMPTY_TYPE) {
            (console as any)[pMethod](...pMessage);
        } else {
            // In the event the type is longer than the spacer, we need to add a padding of one space
            const TYPE_LONGER_THAN_SPACER: boolean = (TYPE.length >= this.TYPE_SPACER_LENGTH);
            // The color to use for the type
            let TYPE_COLOR: string = this.types[TYPE.toLowerCase()] ? this.types[TYPE.toLowerCase()] : this.types.default;
            // Check if the color is ANSI or CSS
            const IS_ANSI: boolean = TYPE_COLOR.includes('\x1b');

            if (!IS_ANSI) {
                TYPE_COLOR = `color: ${TYPE_COLOR}`;
            }

            // Check if running in a browser environment
            const isBrowser: boolean = typeof globalThis.window !== 'undefined';

            if (isBrowser) {
                if (IS_ANSI) {
                    // ANSI colors in browser console (may not be supported by all)
                    (console as any)[pMethod](TYPE_COLOR + TYPE + this.SPACE_CHAR.repeat(Math.max(this.TYPE_SPACER_LENGTH - TYPE.length, TYPE_LONGER_THAN_SPACER ? 1 : 0)) + '|' + this.RESET, ...pMessage);
                } else {
                    // CSS colors in browser console
                    (console as any)[pMethod]('%c' + TYPE + this.SPACE_CHAR.repeat(Math.max(this.TYPE_SPACER_LENGTH - TYPE.length, TYPE_LONGER_THAN_SPACER ? 1 : 0)) + '|', TYPE_COLOR, ...pMessage);
                }
            } else {
                // Node.js or other environments (assume ANSI support)
                (console as any)[pMethod](TYPE_COLOR + TYPE + this.SPACE_CHAR.repeat(Math.max(this.TYPE_SPACER_LENGTH - TYPE.length, TYPE_LONGER_THAN_SPACER ? 1 : 0)) + '|' + this.RESET, ...pMessage);
            }
        }
        // Reset the current type
        this.currentType = '';
    }

    /**
     * Outputs a message to the console.
     * @param pMessage - The message(s) to log
     */
    public log(...pMessage: any[]): void {
        this.message('log', ...pMessage);
    }

    /**
     * Outputs an informational message to the console.
     * @param pMessage - The message(s) to log
     */
    public info (...pMessage: any[]): void {
        this.message('info', ...pMessage);
    }

    /**
     * Outputs an error message to the console.
     * @param pMessage - The message(s) to log
     */
    public error(...pMessage: any[]): void {
        this.message('error', ...pMessage);
    }

    /**
     * Outputs a warning message to the console.
     * @param pMessage - The message(s) to log
     */
    public warn(...pMessage: any[]): void {
        this.message('warn', ...pMessage);
    }

    /**
     * The console.assert() method writes an error message to the console if the assertion is false. If the assertion is true, nothing happens.
     * @param pMessage - A boolean assertion. If false, the error message is written.
     */
    public assert(...pMessage: any[]): void {
        console.assert(...pMessage);
    }

    /**
     * The console.debug() method outputs a message to the web console at the "debug" log level.
     * @param pMessage - The message(s) to log
     */
    public debug(...pMessage: any[]): void {
        this.message('debug', ...pMessage);
    }

    /**
     * The console.count() method logs the number of times that this particular call to count() has been called.
     * @param pLabel - A string. If supplied, count() outputs the number of times it has been called with that label. If omitted, count() behaves as though it was called with the "default" label.
     */
    public count(pLabel?: string): void {
        console.count(pLabel);
    }

    /**
     * The console.countReset() method resets counter used with console.count().
     * @param pLabel - A string. If supplied, countReset() resets the count for that label to 0. If omitted, countReset() resets the default counter to 0.
     * */
    public countReset(pLabel?: string): void {
        console.countReset(pLabel);
    }

    /**
     * The console.table() method displays tabular data as a table.
     * @param pData - The data to display. This must be either an array or an object.
     * @param pColumns - An array containing the names of columns to include in the output.
     */
    public table(pData: any[] | object, pColumns?: string[]): void {
        console.table(pData, pColumns);
    }

    /**
     * The console.time() method starts a timer you can use to track how long an operation takes.
     * @param pLabel - A string representing the name to give the new timer.
     */
    public time(pLabel: string = "default"): void {
        // console.time doesn't support prefixes well, so we pass it through message()
        // which will apply the prefix *before* calling console.time
        this.message('time', pLabel);
    }

    /**
     * The console.timeLog() method logs the current value of a timer that was previously started by calling console.time().
     * @param pLabel - The name of the timer to log to the console. If this is omitted the label "default" is used.
     */
    public timeLog(pLabel: string = "default", ...pData: any[]): void {
        // Pass label and any additional data through message()
        this.message('timeLog', pLabel, ...pData);
    }

    /**
     * The console.timeEnd() stops a timer that was previously started by calling console.time().
     * @param pLabel - A string representing the name of the timer to stop.
     */
    public timeEnd(pLabel: string = "default"): void {
        this.message('timeEnd', pLabel);
    }

    /**
     * The console.trace() method outputs a stack trace to the Web console.
     * @param pMessage - Zero or more objects to be output to console along with the trace.
     */
    public trace(...pMessage: any[]): void {
        this.message('trace', ...pMessage);
    }

    /**
     * The console.group() method creates a new inline group in the Web console log.
     * @param pLabel - Label for the group.
     */
    public group(pLabel: string = ""): void {
        this.message('group', pLabel);
    }

    /**
     * The console.groupCollapsed() method creates a new inline group in the Web Console, but collapsed.
     * @param pLabel - Label for the group.
     */
    public groupCollapsed(pLabel: string = ""): void {
        // Apply prefix via message()
        this.message('groupCollapsed', pLabel);
    }

    /**
     * The console.groupEnd() method exits the current inline group in the Web console.
     */
    public groupEnd(): void {
        // groupEnd doesn't take a label or prefix, just call it directly.
        console.groupEnd();
    }

    /**
     * Clears the console
     */
    public clear(): void {
        console.clear();
    }

    /**
     * Registers a type to this logger
     * @param pType - The type to register to this logger
     * @param pAnsiInfo - The color (ANSI or CSS color string) this type will be when logged
     */
    public registerType(pType: string, pAnsiInfo: string): void {
        if (this.types[pType.toLowerCase()]) return;
        if (typeof(pType) === 'string' && typeof(pAnsiInfo) === 'string') {
            this.types[pType.toLowerCase()] = pAnsiInfo;
        }
    }

    /**
     * Registers the types in the pTypes array.
     * @param pTypes - The type array that contains the types to register.
     */
    public registerTypes(pTypes: { type: string, ansi: string }[]): void {
        if (Array.isArray(pTypes)) {
            for (let i = 0; i < pTypes.length; i++) {
                this.registerType(pTypes[i].type, pTypes[i].ansi);
            }
        }
    }
    
    /**
     * Unregisters a type from this logger
     * @param pType - The type to unregister from this logger
     */
    public unregisterType(pType: string): void {
        const lowerCaseType = pType.toLowerCase();
        if (this.types[lowerCaseType]) {
            delete this.types[lowerCaseType];
        }
    }
}